@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
*{
  margin:0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    display:flex;
   text-align:center;
   color:white;
   background-color:black;
   top:0;
   position:-webkit-sticky;
   position:sticky;
   z-index: 2000;
}
.header_left{
    flex: 0.4 1;
}
.header_left > h1{
    text-align:center;
    font-family:"Alata",sans-serif;
    letter-spacing:1px;
    padding:20px 0;
}
.header_left > h1 > span{
    color:red; 
}
.header_right{
    display:flex;
    flex: 0.6 1;
    justify-content:space-evenly;
    align-items:center;
}
.header_right > a >h4{
    font-size:18px;
    cursor:pointer;
    transition:transform 200ms ease-in-out;
    font-family:"Crimson Text",serif;
    padding:20px 0;
    letter-spacing:1px;
}
.header_right > a> h4:hover{
   transform: scale(1.09);
   background-color:#f64c08;
   color:white;
   padding:5px 20px;
   border-radius:2px;
   letter-spacing:1px;
   font-family:"Crimson Text" ,serif;
   font-size:18px;
   cursor:pointer;  
}
.header_rightButton{
    background-color:#f64c08;
    color:white;
    padding:5px 20px;
    border-radius:2px;
    cursor:pointer;
}
.skillcontainer{
    display:flex;
    align-items:center;
    justify-content:center;
    margin:200px 0;
}
.skillcontainer_text{
    color:white;
    flex:0.6 1;
    text-align:left;
    max-width:500px;
    margin-bottom:20px;
}
.skillcontainer_text > h2{
    font-size:30px;
}
.skillcontainer_image{
    flex:0.4 1;
    text-align:center;
    margin-right:100px; 
}
.skillcontainer_image > img{
    width:600px;
    border-radius:3px;
    object-fit:contain;
}
.skillContainer_skillSet > h5{
    font-family:"Montserrat",sans-serif;
    font-weight:300;
    padding:10px 0px 30px 0px;
}
.skillContainer_slider > .MuiLinearProgress-colorPrimary{
    background-color: rgb(88,88,88);
}
.skillcontainer_slider1 > div> .MuiLinearProgress-barColorPrimary{
    background-color:tomato;
}
.skillcontainer_slider2 > div> .MuiLinearProgress-barColorPrimary{
    background-color:#e3ff47;
}
.skillcontainer_slider3 > div> .MuiLinearProgress-barColorPrimary{
    background-color:#6947ff;
}
.skillcontainer_slider4 > div> .MuiLinearProgress-barColorPrimary{
    background-color:#b41880;
}
.skillcontainer_slider5 > div> .MuiLinearProgress-barColorPrimary{
    background-color:#66ff47;
}
.topContent {
    color:white;
    position:relative;
}
.topContent_container{
    position:absolute;
    top: 300px;
    right: 100px;
}
.topContent_container >p{
    letter-spacing:1px;
    text-align: left;
    padding-left: 3px;
    padding-top:20px;
}
.topContent_container > a > button{
    margin-top:30px;
    text-align:center;
    margin-left: 5px;
    border:none;
    border-radius: 2px;
    outline:none;
}
.topContent_container > h1{
    font-size: 50px;
    font-family:"Montserrat",sans-serif;
}
.topContent_downloadButton{
    color:white;
    background-color:#f64c08;
    font-size: 14px;
    padding :10px 30px;
    font-family:"Montserrat",sans-serif;
    font-weight:100;
    cursor:pointer;
    margin-right:20px;
}
.topContent_downloadButton:hover{
    color: #f64c08;
    background-color:white;
    font-weight:600;
}
.topContent_workButton{
    color:white;
    border:1px solid white !important;
    background-color:transparent;
    font-size:14px;
    padding:9px 45px;
    font-family:"Montserrat",sans-serif;
    font-weight:300;
    cursor:pointer;
}
.topContent_workButton:hover{
    color:black;
    background-color:white;
    font-weight:600;
}
.topContainer{
    background:url(/portfoliowebsite/static/media/Topprofile.5e40e72a.jpg);
    height : 100vh;
    background-position:center;
    background-size:cover;
    background-repeat:no-repeat;
    box-shadow:inset 0 0 0 2000px rgba(0,0,0,0.5);
}
.project > img{
    width:350px;
    height:170px;
}
.project{
    margin:15px;
    width:350px;
    height:170px;
    transition:transform 100ms ease-in;
}
.project:hover{
    color:white;
    background-color:#f64c08;
    transform:scale(1.05);
    display:flex;
    justify-content: center;
    align-items:center;
    cursor:pointer;
    
}
.project_content{
    padding:10px;
}
.project_content >h4{
    padding-bottom:5px;
}
.project_content >p{
    font-size:15px;
}
.projectContainer{
    display:flex;
    flex-direction:column;
    color:white;
    justify-content:center;
    margin:200px 0;
    align-items:center;
}
.projectContainer > h1 {
    padding-bottom:15px;
}
.projectContainer > p{
    padding-bottom:20px;
}
.projectContainer_projects{
    display:flex;
    width:60vw;
    margin-top:50px;
    flex-wrap:wrap;
}
.experience{
    display:flex;
    flex-direction:column;
    border: 2px dotted white;
    border-radius: 10px;
    width:200px;
    height:150px;
    justify-content:center;
    align-items:center;
    background-color:black;
    overflow:hidden;
    color:white;
}
.experience >p{
    font-family: 'Josefin Sans', sans-serif;
    font-size:16px;
    letter-spacing: 1px;
}
.experienceContainer{
    display:flex;
    flex-direction:column;
    color:white;
    justify-content:center;
    margin:200px 0;
    align-items:center;
}
.experienceContainer >h1{
    padding-bottom:20px;
}
.experienceContainer_info{
    background:url("https://www.wallpapers13.com/wp-content/uploads/2016/01/Colorful-Waterfall-Background-9665-915x515.jpg");
    background-position:center;
    background-size:cover;
    height:300px;
    /* z-index: 1000px; */
    width:80vw;
    box-shadow:inset 0 0 0 2000px rgb(0,0,0,0.9);
    display:flex;
    align-items:center;
    justify-content:space-evenly;
}
.contact{
    display:flex;
    flex-direction:column;
    color:white;
    justify-content: center;
    align-items:center;
}
.contact_container{
    border:1px solid white;
    width:600px;
    height:200px;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    padding:20px;
    margin-top:20px;
    margin-bottom:30px;
}
.contact_container > p{
    font-size:20;
    text-align:left;
    padding-bottom:30px;
}
.contact_container >p>span{
    color:#f64c08;
}
.contact_icons > a > button > span > .MuiSvgIcon-root {
    color:white;
    font-size:30px;
}

a{
  text-decoration:none ;
  color:white;
}
